  color: rgba(255,0,0,0.9);
}
.form-item-title .summary {
  font-size: 11px;
  font-weight: normal;
}
.form-btn-wrap {
  text-align: center;
  border-top: 1px solid rgba(186,186,186,0.5);
  padding: 10px;
}
